import BaseApiService from "services/BaseApiService";

// ---------------- Default path ------------------ //
const BASE_PATH = "/custom/api";
const DEFAULT_PATH = `${BASE_PATH}/technical`;

class TechnicalService extends BaseApiService {
  getTechnicalDetails = (stageUid: string) => {
    const url = `${DEFAULT_PATH}/search?stage.uid=${stageUid}&isActive=true`;
    return this.makeGetRequestWithAuth(url);
  };

  getGeneralDetails = (uid: string) => {
    const url = `${DEFAULT_PATH}/general/detail/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };

  getPropertyValuation = (start = 0, limit = 10, search: string) => {
    const url = `${BASE_PATH}/stage/property/valuation/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getPropertyAmenities = (start = 0, limit = 10, search: string) => {
    const url = `${BASE_PATH}/stage/property/amenities/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getPropertyDetails = (uid: string) => {
    const url = `${DEFAULT_PATH}/property/detail/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };

  getValuationAndAmenities = (uid: string) => {
    const url = `${DEFAULT_PATH}/valuation/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };

  getStageOfConstruction = (uid: string) => {
    const url = `${DEFAULT_PATH}/construction/stage/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };

  getPropertyPhotos = (uid: string) => {
    const url = `${DEFAULT_PATH}/property/photo?technicalUid=${uid}`;
    return this.makeGetRequestWithAuth(url);
  };

  getUploadReport = (uid: string) => {
    const url = `${DEFAULT_PATH}/upload/document/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };

  updateGeneralDetails = (payload: any) => {
    const url = `${DEFAULT_PATH}/general/detail`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateDocumentDetails = (payload: any) => {
    const url = `${DEFAULT_PATH}/document/detail`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateSurroundingLocalityDetails = (payload: any) => {
    const url = `${DEFAULT_PATH}/surrounding/detail`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updatePropertyDetails = (payload: any) => {
    const url = `${DEFAULT_PATH}/property/detail`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updatePropertyValuationAndAmenities = (payload: any) => {
    const url = `${DEFAULT_PATH}/valuation`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateStageOfConstruction = (payload: any) => {
    const url = `${DEFAULT_PATH}/construction/stage`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateGovtGuidelines = (payload: any) => {
    const url = `${DEFAULT_PATH}/government/guidelines`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateOccupancyDetails = (payload: any) => {
    const url = `${DEFAULT_PATH}/occupancy/detail`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updatePropertyPhotos = (payload: any) => {
    const url = `${DEFAULT_PATH}/property/photo`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateTechnicalStatus = (payload: any) => {
    const url = `${DEFAULT_PATH}/status`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateUploadReport = (payload: any) => {
    const url = `${DEFAULT_PATH}/upload/document`;
    return this.makePutRequestWithAuth(url, payload);
  };

  /*-------------------Document Exchange----------------- */

  getDocumentExchangeList = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/document/exchange/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  updateDocumentExchange = (payload: any) => {
    const url = `${DEFAULT_PATH}/document/exchange`;
    return this.makePutRequestWithAuth(url, payload);
  };

  /*-------------------------Deviations-------------------*/

  getDeviationNames = (start = 0, limit = 0, search: string) => {
    const url = `${BASE_PATH}/deviation/search?start=${start}&limit=${limit}${search}&isActive=true`;
    return this.makeGetRequestWithAuth(url);
  };

  getTechnicalDeviations = (search: string) => {
    const url = `${DEFAULT_PATH}/deviation${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  updateDeviations = (payload: any) => {
    const url = `${DEFAULT_PATH}/deviation`;
    return this.makePutRequestWithAuth(url, payload);
  };

  /*------------------Stage Complete and Submit--------------------------- */

  stageComplete = (uid: string, payload: any) => {
    const url = `${DEFAULT_PATH}/complete/${uid}`;
    return this.makePutRequestWithAuth(url, payload);
  };

  submit = (uid: string, payload: any) => {
    const url = `${DEFAULT_PATH}/submit/${uid}`;
    return this.makePutRequestWithAuth(url, payload);
  };
}

export default new TechnicalService();
